<template>
  <div class="index container">
    <div class="row mb-4">
      <div class="col">
        <div class="mx-3">
          <img src="@/assets/img/content_01.png" alt="">
        </div>
      </div>
    </div>
    <div v-if="!result">
      <div class="row row-cols-2 gx-2">
        <div class="col mb-2">
          <a href="" @click.prevent="changeBgSrc('bg1')"><img src="@/assets/img/preview-bg1.jpg" alt="" /></a>
        </div>
        <div class="col mb-2">
          <a href="" @click.prevent="changeBgSrc('bg2')"><img src="@/assets/img/preview-bg2.jpg" alt="" /></a>
        </div>
        <div class="col mb-2">
          <a href="" @click.prevent="changeBgSrc('bg3')"><img src="@/assets/img/preview-bg3.jpg" alt="" /></a>
        </div>
        <div class="col mb-2">
          <a href="" @click.prevent="changeBgSrc('bg4')"><img src="@/assets/img/preview-bg4.jpg" alt="" /></a>
        </div>
      </div>
      <div class="row my-2">
        <div class="col">
          <img src="@/assets/img/content_02.png" alt="">
        </div>
      </div>
      <div class="bg-primary text-white" :class="[bgColor==='bg1'||bgColor==='bg4'?'bg-primary':'bg-secondary']">
        <div class="row">
          <div class="col-12">
            <img :src="previewBgSrc" alt="" />
          </div>
        </div>
        <div class="row">
          <div class="col">
            <div class="d-flex px-4">
              <div class="w-70">
                <input
                  type="text"
                  id="inputPassword6"
                  class="form-control border-0 bg-transparent ps-0 border-bottom rounded-0 text-white"
                  placeholder="請填寫您的名字(最多10字)"
                  v-model="inputSenderName"
                  maxlength="10"
                />
              </div>
              <div class="d-flex align-items-center justify-content-end w-30">
                <h6 id="passwordHelpInline" class="fs-6">送來祝福</h6>
              </div>
            </div>
          </div>
        </div>
        <div class="row">
          <div class="col">
            <div class="d-flex px-4">
              <div class="d-flex align-items-center justify-content-start w-20">
                <h6 id="passwordHelpInline" class="fs-6">Dear</h6>
              </div>
              <div class="w-80">
                <input
                  type="text"
                  id="inputPassword6"
                  class="form-control border-0 bg-transparent border-bottom rounded-0 text-white"
                  placeholder="請填寫好友名稱(最多10字)"
                  v-model="inputRecipientName"
                  maxlength="10"
                />
              </div>
            </div>
          </div>
        </div>
        <div class="row">
          <div class="col">
            <div class="px-4">
              <input
                type="text"
                id="inputPassword6"
                class="form-control border-0 bg-transparent border-bottom rounded-0 text-white ps-0"
                placeholder="請填寫祝賀文(最多15字)"
                v-model="inputContent"
                maxlength="15"
              />
              <h6 class="fs-6 mt-2">或選擇下方祝賀訊息</h6>
            </div>
          </div>
        </div>
        <div class="py-4"><img class="mx-auto logo-w" src="@/assets/img/SVG/logo-w.svg" alt=""></div>
      </div>
      <div class="row mt-4">
        <div class="col-12 text-center">
          <a :class="[bgColor==='bg1'||bgColor==='bg4'?'bg-primary':'bg-secondary']" class="d-block text-white py-2 fs-7 hand cursor-pointer" @click.prevent="changeContent(content1)">獻上專屬好禮，感恩有你「香」伴!</a>
        </div>
        <div class="col-12 text-center mt-4">
          <a :class="[bgColor==='bg1'||bgColor==='bg4'?'bg-primary':'bg-secondary']" class="d-block text-white py-2 fs-7 hand cursor-pointer" @click.prevent="changeContent(content2)">願你在馥郁香氣中獲得新年幸運!</a>
        </div>
      </div>
      <div class="event-info row mt-3">
        <div class="col-12 d-flex justify-content-center mb-4"><button type="button" class="btn btn-outline-dark rounded-0" @click="setCanvas">立即分享</button></div>
        <div class="col-12">
          <div class="mx-2"><img src="@/assets/img/content_03.png?v=1100" alt=""></div>
        </div>
      </div>
      <span style="font-family: 'Malone', 'Noto Serif TC'; visibility: hidden; ">.</span>
      <canvas ref="postcardCanvas" :width="width" :height="height" class="postcard-canvas d-none" ></canvas>
    </div>
    <!-- 示意 -->
    <img v-else :src="outputPostcardSrc" alt="">
  </div>
</template>

<script>
import { envConfig } from '@/env'
export default {
  name: 'PostCardMaking',
  data () {
    return {
      publicPath: process.env.BASE_URL,
      postcardBgSrc: 'img/postcard-bg1.jpg',
      previewBgSrc: 'img/preview-bg1.jpg',
      bgColor: 'bg1',
      inputContent: '',
      inputRecipientName: '',
      inputSenderName: '',
      outputPostcardSrc: '',
      content1: '獻上專屬好禮，感恩有你「香」伴!',
      content2: '願你在馥郁香氣中獲得新年幸運!',
      result: false
    }
  },
  methods: {
    setCanvas () {
      const scale = 2
      const canvas = this.$refs.postcardCanvas
      canvas.width = this.width * scale
      canvas.height = this.height * scale
      const ctx = canvas.getContext('2d')
      function drawText (text, centerX, centerY, fontsize, lineNum) {
        const lineheight = 15 * scale
        ctx.save()
        ctx.font = fontsize * scale + 'px ' + 'Malone, Noto Serif TC'
        ctx.textAlign = 'left'
        ctx.fillStyle = 'white'
        ctx.textBaseline = 'top'
        ctx.fillText(text, centerX, centerY + (lineheight * lineNum))
        ctx.restore()
      }
      const postcard = new Image()
      postcard.src = `${this.publicPath}${this.postcardBgSrc}`
      postcard.onload = () => {
        ctx.drawImage(postcard, 0, 0, ctx.canvas.width, ctx.canvas.width / 0.7421)
        drawText(this.renderPostcardSenderName, 30 * scale, canvas.height / 2 + (10 * scale), 18, 0)
        drawText(this.renderPostcardRecipientName, 30 * scale, canvas.height - (170 * scale), 14, 0)
        drawText(this.renderPostcardContent, 30 * scale, canvas.height - (170 * scale), 14, 2)
        this.outputPostcardSrc = ctx.canvas.toDataURL('image/jpeg', 1.0)
        this.result = true
        // this.sendPostcard()
      }
    },
    changeContent (text) {
      this.inputContent = text
    },
    changeBgSrc (bg) {
      this.postcardBgSrc = `${this.publicPath}img/postcard-${bg}.jpg`
      this.previewBgSrc = `${this.publicPath}img/preview-${bg}.jpg`
      this.bgColor = bg
    },
    sendPostcard () {
      const api = `${envConfig().baseUrl}/MgmV3/getShareUrl`
      const newSrc = this.outputPostcardSrc.replace('data:image/jpeg;base64,', '')
      const apiData = {
        activity: this.getUrlParams(),
        image: newSrc
      }
      this.$http.post(api, apiData)
        .then((res) => {
          // this.lineTag()
          const url = res.data.shareUrl
          window.location.href = url
        })
        .catch(err => {
          this.$swal({
            icon: 'error',
            html: err.response.data.error
          }).then((result) => {
            const url = err.response.data.line_me
            window.location.href = url
          })
        })
    },
    getUrlParams () {
      const urlSearchParams = new URLSearchParams(window.location.search)
      const params = Object.fromEntries(urlSearchParams.entries())
      return params.activity
    },
    lineTag () {
      // console.log(window._lt, 'window._lt vue')
      window._lt('send', 'cv', {
        type: 'Sharetofriend'
      }, ['42b54ecd-d4c6-41e6-b77f-d3e98f6fcd35'])
    }
  },
  computed: {
    width: function () {
      return 350
    },
    height: function () {
      return (this.width) / 0.7421
    },
    renderPostcardContent: function () {
      return this.inputContent
    },
    renderPostcardSenderName: function () {
      return `${this.inputSenderName}送來的祝福`
    },
    renderPostcardRecipientName: function () {
      return `Dear ${this.inputRecipientName},`
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
.logo-w {
  width: 40%;
}
.w-30 {
  width: 30%;
}
.w-70 {
  width: 70%;
}
.w-20 {
  width: 20%;
}
.w-80 {
  width: 80%;
}
.event-info {
  letter-spacing: .1rem;
}
.text-justify {
  text-align: justify;
  text-justify: inter-ideograph;
}

input {
  letter-spacing: .11rem;
}

.hand {
  --hand-width: 5%;
  @media (max-width: 414px) {
    --hand-width: 6%;
  }
  position: relative;
  &::after {
    animation: move 1s ease infinite ;
    animation-delay: 0.4s;
    position: absolute;
    display: block;
    content: '';
    width: var(--hand-width);
    height: 50px;
    background: url('../assets/img/hand-icon.png') no-repeat top center;
    background-size: 100%;
    right: 5%;
    bottom: -100%;
    pointer-events: none;
  }
}

@keyframes move {
  0% {
     transform: scale(1);
  }
  50% {
     transform: scale(1.2);
  }
  100% {
    transform: scale(1);

  }

}
.cursor-pointer {
  cursor: pointer;
}
</style>
