export const envConfig = () => {
  const hostname = window.location.hostname
  const envData = {
    baseUrl: ''
  }
  if (hostname === '10.10.50.180') { // 測試機
    envData.baseUrl = 'https://sandy-jomalone.dev.coolbe.com.tw'
  } else { // 開發
    envData.baseUrl = `https://${hostname}`
  }
  return envData
}
